"use client";

import { useEffect, useRef, useState } from "react";
import { Button } from "@/components/ui/button";
import QrScanner from "qr-scanner";

interface QRScannerProps {
  onScan: (data: string) => void;
  onClose: () => void;
}

export default function QRScanner({ onScan, onClose }: QRScannerProps) {
  const videoRef = useRef<HTMLVideoElement>(null);
  const [scanner, setScanner] = useState<QrScanner | null>(null);
  const [error, setError] = useState<string>("");
  const [isScanning, setIsScanning] = useState(false);

  useEffect(() => {
    if (!videoRef.current) return;

    const qrScanner = new QrScanner(
      videoRef.current,
      (result) => {
        if (result?.data) {
          onScan(result.data);
          onClose();
        }
      },
      {
        onDecodeError: (err) => {
          console.log("Decode error:", err);
        },
        highlightScanRegion: true,
        highlightCodeOutline: true,
      }
    );

    setScanner(qrScanner);

    qrScanner
      .start()
      .then(() => {
        setIsScanning(true);
        setError("");
      })
      .catch((err) => {
        console.error("Failed to start QR scanner:", err);
        setError("Failed to access camera. Please check permissions.");
      });

    return () => {
      qrScanner.stop();
      qrScanner.destroy();
    };
  }, [onScan, onClose]);

  const handleClose = () => {
    if (scanner) {
      scanner.stop();
      scanner.destroy();
    }
    onClose();
  };

  return (
    <div className="space-y-4">
      {error ? (
        <div className="text-center space-y-4">
          <div className="text-red-400 font-mono text-sm">
            ‚ùå {error}
          </div>
          <div className="text-green-300 font-mono text-xs">
            Please ensure camera permissions are granted
          </div>
        </div>
      ) : (
        <div className="space-y-4">
          <div className="relative">
            <video
              ref={videoRef}
              className="w-full h-64 bg-black border border-green-500 rounded"
              style={{ objectFit: "cover" }}
            />
            {!isScanning && (
              <div className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-50">
                <div className="text-green-400 font-mono">Starting camera...</div>
              </div>
            )}
          </div>
          
          <div className="text-center text-green-300 font-mono text-sm">
            üì∑ Point camera at QR code to scan
          </div>
        </div>
      )}

      <div className="flex justify-center">
        <Button
          onClick={handleClose}
          variant="outline"
          className="border-red-500 text-red-400 hover:bg-red-500 hover:text-black font-mono"
        >
          CLOSE
        </Button>
      </div>
    </div>
  );
}
