"use client";

import { useState, useRef, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";

interface CommandPromptProps {
  onCommand: (command: string) => void;
  commandHistory: string[];
  setCommandHistory: React.Dispatch<React.SetStateAction<string[]>>;
}

export default function CommandPrompt({ onCommand, commandHistory, setCommandHistory }: CommandPromptProps) {
  const [currentCommand, setCurrentCommand] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const scrollRef = useRef<HTMLDivElement>(null);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [commandHistory]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!currentCommand.trim() || isLoading) return;

    const command = currentCommand.trim();
    setCurrentCommand("");

    // Handle Sage AI queries
    if (command.toLowerCase().startsWith("sage ")) {
      const query = command.slice(5);
      setIsLoading(true);
      
      try {
        const response = await fetch("/api/sage", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ query }),
        });

        const data = await response.json();
        
        if (response.ok) {
          setCommandHistory(prev => [
            ...prev.slice(0, -1), // Remove the "thinking..." message
            `ü§ñ Sage: ${data.response}`,
            "---"
          ]);
        } else {
          setCommandHistory(prev => [
            ...prev.slice(0, -1),
            `‚ùå Sage Error: ${data.error || "Failed to get response"}`,
            "---"
          ]);
        }
      } catch (error) {
        setCommandHistory(prev => [
          ...prev.slice(0, -1),
          `‚ùå Sage Error: Failed to connect to AI service`,
          "---"
        ]);
      } finally {
        setIsLoading(false);
      }
    } else {
      // Handle other commands
      onCommand(command);
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === "Enter") {
      handleSubmit(e);
    }
  };

  return (
    <div className="space-y-4">
      {/* Command History */}
      <ScrollArea className="h-64 w-full border border-green-500 bg-black p-3 rounded">
        <div ref={scrollRef} className="space-y-1">
          {commandHistory.map((line, index) => (
            <div
              key={index}
              className={`font-mono text-sm ${
                line.startsWith(">") 
                  ? "text-yellow-400" 
                  : line.startsWith("‚ùå") 
                  ? "text-red-400"
                  : line.startsWith("‚úÖ") 
                  ? "text-green-400"
                  : line.startsWith("ü§ñ") 
                  ? "text-blue-400"
                  : line.startsWith("üì∑") || line.startsWith("üì¶")
                  ? "text-cyan-400"
                  : line === "---"
                  ? "text-gray-600"
                  : "text-green-300"
              }`}
            >
              {line}
            </div>
          ))}
        </div>
      </ScrollArea>

      {/* Command Input */}
      <form onSubmit={handleSubmit} className="flex space-x-2">
        <div className="flex-1 flex items-center space-x-2">
          <span className="text-green-400 font-mono">$</span>
          <Input
            ref={inputRef}
            value={currentCommand}
            onChange={(e) => setCurrentCommand(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder="Enter command..."
            className="bg-black border-green-500 text-green-400 font-mono placeholder-green-600"
            disabled={isLoading}
          />
        </div>
        <Button
          type="submit"
          disabled={!currentCommand.trim() || isLoading}
          className="bg-green-600 hover:bg-green-700 text-black font-mono"
        >
          {isLoading ? "..." : "EXEC"}
        </Button>
      </form>

      {/* Status */}
      <div className="text-xs font-mono text-green-600">
        Status: {isLoading ? "Processing..." : "Ready"} | Commands: help, scan, track, sage, clear
      </div>
    </div>
  );
}
