"use client";

import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Card, CardContent } from "@/components/ui/card";

interface Tool {
  id: string;
  name: string;
  qrCode: string;
  dateAdded: Date;
}

interface ToolListProps {
  tools: Tool[];
  setTools: React.Dispatch<React.SetStateAction<Tool[]>>;
}

export default function ToolList({ tools, setTools }: ToolListProps) {
  const removeTool = (id: string) => {
    setTools(prev => prev.filter(tool => tool.id !== id));
  };

  const clearAllTools = () => {
    setTools([]);
  };

  const formatDate = (date: Date) => {
    return date.toLocaleDateString() + " " + date.toLocaleTimeString([], { 
      hour: '2-digit', 
      minute: '2-digit' 
    });
  };

  if (tools.length === 0) {
    return (
      <div className="text-center space-y-4">
        <div className="text-green-300 font-mono text-sm">
          üì¶ No tools tracked yet
        </div>
        <div className="text-green-600 font-mono text-xs">
          Use 'scan' command to add tools
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div className="text-green-400 font-mono text-sm">
          üì¶ Total: {tools.length} tool{tools.length !== 1 ? 's' : ''}
        </div>
        <Button
          onClick={clearAllTools}
          variant="outline"
          size="sm"
          className="border-red-500 text-red-400 hover:bg-red-500 hover:text-black font-mono text-xs"
        >
          CLEAR ALL
        </Button>
      </div>

      {/* Tools List */}
      <ScrollArea className="h-64 w-full">
        <div className="space-y-2">
          {tools.map((tool) => (
            <Card key={tool.id} className="bg-gray-800 border-green-600">
              <CardContent className="p-3">
                <div className="flex justify-between items-start">
                  <div className="flex-1 space-y-1">
                    <div className="text-green-400 font-mono text-sm font-bold">
                      {tool.name}
                    </div>
                    <div className="text-green-300 font-mono text-xs break-all">
                      QR: {tool.qrCode}
                    </div>
                    <div className="text-green-600 font-mono text-xs">
                      Added: {formatDate(tool.dateAdded)}
                    </div>
                  </div>
                  <Button
                    onClick={() => removeTool(tool.id)}
                    variant="outline"
                    size="sm"
                    className="border-red-500 text-red-400 hover:bg-red-500 hover:text-black font-mono text-xs ml-2"
                  >
                    DEL
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </ScrollArea>

      {/* Stats */}
      <div className="text-xs font-mono text-green-600 text-center">
        Last scan: {tools.length > 0 ? formatDate(tools[tools.length - 1].dateAdded) : 'Never'}
      </div>
    </div>
  );
}
